













































































































import Vue from 'vue'
import { isMobile } from 'mobile-device-detect';
import { MetronomeNote, MetronomeBeater } from '@/plugins/Metronome'
interface Volume {
  title: string;
  icon: string;
  value: number;
  slider: {
      min: number;
      max: number;
  };
  metronomeNote: MetronomeNote;
}

interface Volumes {
  stress: {
    title: string;
    value: number;
    slider: {
      min: number;
      max: number;
    };
    metronomeNote: MetronomeNote;
  };
  quarter: Volume;
  quaver: Volume;
  semiquaver: Volume;
  triplet: Volume;
}

export default Vue.extend({
  props: {
    dark: Boolean,
    isVirtualKeyboardShown: Boolean
  },
  data: () => ({
    isMobile: isMobile,
    title: '',
    active: false,
    isPlaying: false,
    items: {
      tempo: {
        title: '',
        value: 60,
        slider: {
          min: 40,
          max: 300
        }
      },
      timeSignature: {
        title: '',
        value: 4,
        complexValue: '',
        select: [1, 2, 3, 4, '']
      },
    },
    volumes: {
      stress: {
        title: '',
        value: 100,
        slider: {
          min: 0,
          max: 100
        },
        metronomeNote: new MetronomeNote(0, 100, 2400)
      },
      quarter: {
        title: '4',
        icon: 'q',
        value: 50,
        slider: {
          min: 0,
          max: 100
        },
        metronomeNote: new MetronomeNote(12, 80, 1200)
      },
      quaver: {
        title: '8',
        icon: 'e',
        value: 0,
        metronomeNote: new MetronomeNote(6, 0, 1200),
        slider: {
          min: 0,
          max: 100
        }
      },
      semiquaver: {
        title: '16',
        icon: 's',
        value: 0,
        metronomeNote: new MetronomeNote(3, 0, 1200),
        slider: {
          min: 0,
          max: 100
        }
      },
      triplet: {
        title: '3',
        icon: 'T',
        value: 0,
        metronomeNote: new MetronomeNote(4, 0, 1200),
        slider: {
          min: 0,
          max: 100
        }
      },
    } as Volumes,
    beater: undefined as MetronomeBeater | undefined
  }),
  methods: {
    metroHandler() {
      this.isPlaying = !this.isPlaying
      if (this.isPlaying) {
        this.start()
      } else  {
        this.stop()
      }
    },
    start() {
      this.beater = new MetronomeBeater({
        stress: this.volumes.stress.metronomeNote,
        quarter: this.volumes.quarter.metronomeNote,
        quaver: this.volumes.quaver.metronomeNote,
        semiquaver: this.volumes.semiquaver.metronomeNote,
        triplet: this.volumes.triplet.metronomeNote,
      })
      const timeSignatureValue = this.items.timeSignature.value
      const timeSignatureNumerator = Number.isInteger(timeSignatureValue) 
        ? [timeSignatureValue] 
        : (this.items.timeSignature.complexValue || "2+3").split(/[^\d]/).map((str) => Number(str))
      this.beater.start(this.items.tempo.value, timeSignatureNumerator)
    },
    stop() {
      this.beater && this.beater.stop()
    },
  },
  watch: {
    'items.timeSignature.value': function () {
      const timeSignatureValue = this.items.timeSignature.value
      const timeSignatureNumerator = Number.isInteger(timeSignatureValue) 
        ? [timeSignatureValue] 
        : (this.items.timeSignature.complexValue || "2+3").split(/[^\d]/).map((str) => Number(str))
      if (this.isPlaying && this.beater !== undefined) {
        this.beater.timeSignatureNumerator = timeSignatureNumerator
      }
    },
    'items.timeSignature.complexValue': function () {
      const timeSignatureNumerator = (this.items.timeSignature.complexValue || "2+3").split(/[^\d]/).map((str) => Number(str))
      if (this.isPlaying && this.beater !== undefined) {
        this.beater.timeSignatureNumerator = timeSignatureNumerator.filter(Boolean)
      }
    },
    'items.tempo.value': function (tempo: number) {
      if (this.beater !== undefined) {
        this.beater.tempo = tempo
      }
    },
  }
})
