





































































































import Vue, { PropType } from 'vue'

export default Vue.extend({
  props: {
    waveform: String,
    sustainMethod: String as PropType<"momentary" | "exclusive" | "alternative">,
    toneRange: Number,
    isMajor: Boolean,
    musicalKey: String,
    dark: Boolean
  },
  data: () => ({
    waveforms: {
      title: '',
      select: [
        {waveform: "sine", text: ''},
        {waveform: "square", text: ''},
        {waveform: "triangle", text: ''},
        {waveform: "sawtooth", text: ''}
      ]
    },
    sustainMethods: {
      value: 'momentary',
      items: {
        momentary: '',
        exclusive: '',
        alternative: ''
      }
    },
    scale: {
      title: '',
      musicalKey: {
        items: ['', 'A', 'Bb', 'B', 'C', 'C#', 'D', 'Eb', 'E', 'F', 'F#', 'G', 'Ab']
      },
      scale: {
        items: {
          major: {
            title: '',
            value: true,
          },
          minor: {
            title: '',
            value: false,
          },
        }
      }
    },
    toneRanges: {
      value: 2,
      items: ["1", "2", "", "1", "2"]
    }
  })
})
